import React, { Component } from 'react'
import {nanoid} from 'nanoid'
import { UserOutlined } from '@ant-design/icons';
import { Input, Button } from 'antd';
import { connect } from 'react-redux';
import { createAddPerson } from '../../redux/action/person';

 class Person extends Component {
    addPerson = ()=>{
      const name = this.name.nativeElement.value;
      const age = this.age.nativeElement.value
        const perObj = {id:nanoid(),name,age}
        this.props.addPerson(perObj)

    }
  render() {
    return (
      <div>
        <h2>person,{this.props.sum}</h2>
        <Input ref={c => this.name = c}  placeholder="" style={{ width: 150 }} prefix={<UserOutlined />} />
        <Input ref={c => this.age = c} style={{ width: 150 }}  placeholder="" />
      
        <Button onClick={this.addPerson}></Button>
        <ul>
          {
            this.props.persons.map((item)=>{
              return <li key={item.id}>{item.name} -- {item.age}</li>
            })
          }
        </ul>
      </div>
    )
  }
}
export default connect(
  state=>({
 persons:state.persons,
 sum:state.sum
}), //
{
  addPerson: createAddPerson  // 
})(Person)
