import { connect } from 'react-redux'
import { createDecrementAction,createIncrementAsyncAction,createIncrementAction } from '../../redux/action/count';
import React, { Component } from 'react'
import { Select, Button, Space } from 'antd'

// ui
 class Count extends Component {
    state = {
        options: [
            { value: 1 },
            { value: 2 },
            { value: 3 }
        ],
        value: '1'
    }

    handleChange = (value) => {
        console.log(`selected ${value}`);
        this.setState({ value: value })
    };
    // 
    handleIncrement = () => {
        const { value } = this.state
        this.props.jia(value * 1)
    }
    // 
    handleDecrement = () => {
        const { value } = this.state
        this.props.jian(value * 1)
    }
    // 
    handleIncrementIfOdd = () => {
        const { value } = this.state
        if (this.props.count % 2 !== 0) {
            this.props.jia(value * 1)
        }
    }
    // 
    handleIncrementAsync = () => {
        const { value } = this.state
        this.props.jianAsync(value * 1, 500)
    }
    render() {
        return (
            <div>
                <h1>{this.props.count},{this.props.persons}</h1>
                <Select defaultValue={this.state.value} style={{ width: 120 }} onChange={this.handleChange} options={this.state.options}>
                </Select>&nbsp;
                <Space >
                    <Button onClick={this.handleIncrement}>+</Button>
                    <Button onClick={this.handleDecrement}>-</Button>
                    <Button onClick={this.handleIncrementIfOdd}></Button>
                    <Button onClick={this.handleIncrementAsync}></Button>
                </Space>

            </div>
        )
    }
}

export default connect(
    state => ({ count: state.sum,persons:state.persons.length }),
    //  
    // dispatch=>(
    //     {
    //         jia: number => dispatch(createIncrementAction(number)),
    //         jian: number => dispatch(createDecrementAction(number)),
    //         jianAsync: (number, time) => dispatch(createIncrementAsyncAction(number, time))
    //     })

    //  react-redux action
    {
        jia: createIncrementAction,
        jian: createDecrementAction,
        jianAsync: createIncrementAsyncAction
    }
)(Count);